
@media screen and (max-width:641px) {
.slide-enter,
  .slide_back-enter {
    position: absolute;
    width: 100%;
}
.slide-leave,
  .slide_back-leave {
    position: absolute;
    width: 100%;
    height:100%;
}
.slide-enter-active,
  .slide_back-enter-active {
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}
.slide-leave-active {
    position: absolute;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.slide-enter{
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}
.slide_back-leave-active {
    position: absolute;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    -webkit-transform: translate(100%);
            transform: translate(100%);
}
.slide_back-enter {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}
body,ul,h3,ol,p{
      margin: 0;
      padding: 0;
}
body{
  font-family: '微软雅黑';
  font-size: 0.28rem;
  max-width: 640px;
  margin:0 auto;
}
::-webkit-scrollbar{  /*不支持火狐*/
      display: none;
}
*{margin:0;padding:0;
}
li{list-style: none;
}
input { outline:0px ; -webkit-appearance: none;
} 



